/* Collapsible component. */
export default function() {
  let details = document.querySelectorAll( "details" );

  details.forEach( el => {
    el.addEventListener( "toggle", e => {
      let summary = e.target.querySelector( "summary" ),
        detailEls = e.target.querySelectorAll( ":not(summary)" )

      detailEls.forEach( el => {
        el.setAttribute( 'aria-hidden', !e.target.open );
      });
      summary.setAttribute( 'aria-expanded', e.target.open );
    });

    /* Disclosed elements should be hidden when `esc` is pressed. */
    el.addEventListener( "keyup", e => {
      if( e.key.toLowerCase() === "escape" ) {
        e.target.closest( "[open]" ).removeAttribute( "open" );
      }
    });
  });
}